/* Global Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
  line-height: 1.6;
  background-color: #f4f4f4;
  color: #333;
}

header {
  background-color: #333;
  color: #fff;
  padding: 1rem;
  text-align: center;
}

nav ul {
  list-style: none;
  display: flex;
  justify-content: center;
}

nav ul li {
  margin: 0 1rem;
}

nav ul li a {
  color: #fff;
  text-decoration: none;
  font-weight: bold;
}

nav ul li a:hover {
  color: #ff6347;
}

/* Hero Section */
.hero {
  height: 100vh;
  background: linear-gradient(135deg, #74ebd5 0%, #ACB6E5 100%);
  display: flex;
  justify-content: center;
  align-items: center;
}

.hero-content {
  text-align: center;
  color: #fff;
}

.hero-content h1 {
  font-size: 4rem;
  margin-bottom: 1rem;
  animation: fadeIn 2s ease-in-out;
}

.hero-content p {
  font-size: 1.5rem;
  animation: fadeIn 3s ease-in-out;
}

@keyframes fadeIn {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

/* About Section */
#about {
  padding: 2rem;
  background-color: #fff;
  text-align: center;
}

#about h2 {
  margin-bottom: 1rem;
}

#about p {
  font-size: 1.2rem;
  line-height: 1.8;
  max-width: 600px;
  margin: auto;
}

/* Projects Section */
#projects {
  padding: 2rem;
  background-color: #f4f4f4;
  text-align: center;
}

.project-grid {
  display: flex;
  justify-content: center;
  gap: 2rem;
}

.project-grid div {
  background-color: #fff;
  padding: 1rem;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.project-grid div:hover {
  transform: scale(1.05);
}

/* Contact Section */
#contact {
  padding: 2rem;
  background-color: #fff;
  text-align: center;
}

#contact ul {
  list-style: none;
  display: flex;
  justify-content: center;
}

#contact ul li {
  margin: 0 1rem;
}

#contact ul li a {
  text-decoration: none;
  color: #333;
  font-weight: bold;
}

#contact ul li a:hover {
  color: #ff6347;
}

/* Footer */
footer {
  background-color: #333;
  color: #fff;
  text-align: center;
  padding: 1rem;
}
